import React, { useState, useEffect } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import { Form, Button, FormControl, Nav, NavDropdown } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import authService from '../services/authService';

function Header() {
  const [isLoggedIn, setLoggedIn] = useState([]);

  useEffect(() => {
    const retrieveUser = async () => {
      const data = await authService.isLoggedIn();
      setLoggedIn(data);
    };
    retrieveUser();
  });

  return (
    <Navbar bg='dark' variant='dark' expand='lg'>
      <div className='container'>
        <Navbar.Brand as={Link} to='/'>
          ATP
        </Navbar.Brand>
        <Navbar.Toggle aria-controls='basic-navbar-nav' />
        <Navbar.Collapse id='basic-navbar-nav'>
          <Nav className='mr-auto'>
            <Nav.Link as={Link} to='/'>
              Home
            </Nav.Link>
            <Nav.Link as={Link} to='/assets'>
              Assets
            </Nav.Link>
            <Nav.Link as={Link} to='/contact'>
                Contact
            </Nav.Link>
            <Nav.Link as={Link} to='/statistics'>
                Stats
            </Nav.Link>
          </Nav>
          <Form inline>
            <FormControl type='text' placeholder='Search' className='mr-sm-2' />
            <Button variant='outline-success'>Search</Button>
          </Form>
          <RightButton />
        </Navbar.Collapse>
      </div>
    </Navbar>
  );

  function RightButton() {
    if (isLoggedIn) {
      return (
        <Button className='ml-2' variant='outline-primary' as={Link} to='/profile'>
          Profile
        </Button>
      );
    } else {
      return (
        <Button className='ml-2' variant='outline-primary' as={Link} to='/login'>
          Login
        </Button>
      );
    }
  }
}

export default Header;
